<style lang="stylus">
.cover-tips
  position fixed
  top 0
  left 0
  width 100%
  height 100%
  background-color rgba(0, 0, 0, 0.5)
  text-align center
  .box
    position relative
    top 50%
    left 50%
    width 220px
    height 90px
    transform translateX(-50%) translateY(-50%)
    background-color #fff
    border-radius 5px
    p
      width 90%
      margin auto
      color #555
      font-size 16px
    .btn-right
      margin-top 10px
      font-size 20px
  .middle
    position relative
    top 50%
    left 50%
    transform translateX(-50%) translateY(-50%)
</style>

<template lang="jade">
.cover-tips(v-show="show")
  .box
    .middle
      p {{text}}
      i(:class="[iconClass]")
</template>

<script>
import vent from 'lib/vent'

export default {
  data () {
    return {
      show: false,
      text: '',
      iconClass: 'btn-right'
    }
  },
  mounted () {
    var vm = this
    vent.$on('coverTips', (text, iconClass, time) => {
      vm.show = true
      vm.text = text || ''
      vm.iconClass = iconClass
      setTimeout(() => {
        vm.show = false
      }, time)
    })

  }
}
</script>





