  .content
    img.title(src="../assets/pc/title-1.png")
</template>

<script>
import compInput from 'components/compInput'
export default {
  components: {
    compInput
  },
  data () {
    return {}
  },
  computed: {},
  methods: {},
  mounted () {}
}
</script>



