<style lang="stylus">
.main-page
  position relative
  width 100%
  height 100%
  .header
    width 100%
  .content
    text-align center
    .input
      width 250px
      margin-top 20px
      color #444
      font-size 15px
      text-align center
      line-height 2.5em
      border-radius 5px
      border 1px solid #aaa
      transition 0.5s border
      &:focus
        border-color #ed2285
    .sbtn
      margin auto
      margin-top 20px
    .tips
      color #aaa
      font-size 12px
      line-height 2em
    .check-record
      margin-top 20px
      font-size 18px
</style>

<template lang="jade">
.main-page
  header.header
    img(src="../assets/bg-2.jpg")
  .content
    input.input(type="text", v-model="name", placeholder="您的姓名")
    input.input(type="text", v-model="department", placeholder="您的部门（精确到最末端部门）")
    input.input(type="text", v-model="guy_name", placeholder="您推荐的学生姓名")
    input.input(type="text", v-model="guy_phone", placeholder="他/她的手机号码")
    input.input(type="text", v-model="guy_email", placeholder="他/她的邮箱")
    .sbtn(@click="sendPush") 我要内推
    p.tips 提交后可以继续填写您推荐的下一个牛人
    p.check-record.sred(@click="redirect('check-record')") 查询内推记录
</template>

<script>
import utils from 'lib/utils'
export default {
  data () {
    return {
      name: '',
      department: '',
      guy_name: '',
      guy_phone: '',
      guy_email: ''
    }
  },
  methods: {
    sendPush () {
      var vm = this
      var src = {
        name: vm.name,
        department: vm.department,
        guy_name: vm.guy_name,
        guy_phone: vm.guy_phone,
        guy_email: vm.guy_email
      }
      var errMsg = checkSrc(src)
      if (errMsg) {
        utils.alert(errMsg)
        return
      }
      utils.ajax({
        url: 'index.php?r=push%2Fadd&' + getData(src),
        type: 'GET',
        dataType: 'jsonp'
      }).then(() => {
        vm.guy_name = ''
        vm.guy_phone = ''
        vm.guy_email = ''
        utils.alert('提交成功', true)
      }).catch((data) => {
        errMsg = data && data.msg ? data.msg : '网络返回异常'
        utils.alert(errMsg)
      })
    },
    redirect (page) {
      utils.redirect(page)
    }
  }
}

function checkSrc (src) {
  var errMsg = ''
  if (!src.name || src.name.length < 2) {
    errMsg = '姓名有误'
  } else if (!src.department) {
    errMsg = '部门有误'
  } else if (!src.guy_name || src.guy_name.length < 2) {
    errMsg = '推荐人姓名有误'
  } else if (!src.guy_phone || src.guy_phone.length != 11) {
    errMsg = '推荐人手机有误'
  } else if (!src.guy_email || !utils.isEmail(src.guy_email)) {
    errMsg = '推荐人邮箱有误'
  }
  return errMsg
}

function getData (src) {
  var data = ''
  for (var key in src) {
    data += '&{key}={value}'.replace('{key}', getDataKey(key)).replace('{value}', src[key])
  }
  return data.substr(1)
}

function getDataKey (key) {
  return 'PushRecord[{key}]'.replace('{key}', key)
}
</script>





