<style lang="stylus">
@import 'assets/css/normalize.styl'
@import 'assets/css/common.styl'
html
  margin auto
  max-width 375PX
body
  background-color #fff
#app
  position relative
  min-height 600PX
  padding-bottom 60px

@media screen and (min-width: 800px)
  html
    max-width none
    #app
      height 100%
      padding-bottom 0
</style>

<template lang="jade">
#app
  component(:is="page")
  cover-tips
  footer-tips
</template>

<script>
import vent from 'lib/vent'
import index from 'pages/index'
import mainPage from 'pages/mainPage'
import checkRecord from 'pages/checkRecord'
import compIndex from 'pages/compIndex'
import coverTips from 'components/coverTips'
import footerTips from 'components/footerTips'

export default {
  data () {
    return {
      page: 'index'
    }
  },
  components: {
    index,
    mainPage,
    compIndex,
    checkRecord,
    coverTips,
    footerTips
  },
  mounted () {
    var vm = this
    if (document.body.clientWidth > 800) {
      vm.page = 'compIndex'
    }
    vent.$on('redirect', (page) => {
      if (page) {
        vm.page = page
        window.scrollTo(0, 0)
      }
    })
  }
}
</script>





