import $ from 'jquery'
import vent from 'lib/vent'

export default {
  isEmail (email) {
    return   /^([a-zA-Z0-9._-])+@([a-zA-Z0-9_-])+((\.[a-zA-Z0-9_-]{2,3}){1,2})$/.test(email)
  },
  alert (msg, hasIcon, time) {
    vent.$emit('coverTips', msg, hasIcon ? 'btn-right' : 'miao', time ? time : 1500)
  },
  redirect (page) {
    vent.$emit('redirect', page)
  },
  ajax (opt) {
    return new Promise(function(resolve, reject) {
      var option = Object.assign(opt, {
        success (data) {
          if (data.code != 200) {
            reject(data)
            return
          }
          resolve(data.data)
        },
        error (data) {
          reject(data)
        }
      })
      option.url = 'http://192.168.91.120:8000/web/' + opt.url
      $.ajax(option)
    })

  }
}



