<style lang="stylus">
.main-page
  .content
    min-height 300px
  .answer
    width 80%
    height 80%
    margin 75px auto
    color #666
</style>

<template lang="jade">
.main-page
  header.header
    img(src="../assets/bg-3.jpg")
  .content(v-if="!isAnswer")
    input.input(type="text", v-model="push.name", placeholder="您的姓名")
    input.input(type="text", v-model="push.guy_name", placeholder="您推荐的学生姓名")
    input.input(type="text", v-model="push.guy_phone", placeholder="他/她的手机号码")
    .sbtn(@click="sendPush") 查询
    p.tips 查询我的内推记录
    p.check-record.sred(@click="redirect('main-page')") 返回
  .content(v-else)
    p.answer(v-if="!hasRecord")
      | 此学生尚未推荐哦~
      br
      | 请点击返回，提交其相关信息
    p.answer(v-else)
      | 抱歉，您已推荐过{{push.guy_name}}，
      br
      | 请勿重复操作，谢谢！
      br
      | 内推信息将在各阶段结束之后反馈给您，
      br
      | 请注意查收！
    .sbtn(@click="isAnswer = false") 返回
</template>

<script>
import $ from 'jquery'
import utils from 'lib/utils'
export default {
  data () {
    return {
      push: {},
      isAnswer: false,
      hasRecord: true
    }
  },
  methods: {
    sendPush () {
      var vm = this
      var src = vm.push
      var errMsg = checkSrc(src)
      if (errMsg) {
        utils.alert(errMsg)
        return
      }
      utils.ajax({
        url: 'index.php?r=push%2Fone&' + getData(src),
        type: 'GET',
        dataType: 'jsonp'
      }).then((data) => {
        vm.isAnswer = true
        vm.hasRecord = !(data instanceof Array)
      }).catch((data) => {
        errMsg = data && data.msg ? data.msg : '网络返回异常'
        utils.alert(errMsg)
      })
    },
    redirect (page) {
      utils.redirect(page)
    }
  }
}

function checkSrc (src) {
  var errMsg = ''
  if (!src.name || src.name.length < 2) {
    errMsg = '姓名有误'
  } else if (!src.guy_name || src.guy_name.length < 2) {
    errMsg = '推荐人姓名有误'
  } else if (!src.guy_phone || src.guy_phone.length != 11) {
    errMsg = '推荐人手机有误'
  }
  return errMsg
}

function getData (src) {
  var data = ''
  for (var key in src) {
    data += '&{key}={value}'.replace('{key}', getDataKey(key)).replace('{value}', src[key])
  }
  return data.substr(1)
}

function getDataKey (key) {
  return '{key}'.replace('{key}', key)
}
</script>





